:root {
  color-scheme: light dark;
}

.body-theme, .footer-theme {
  background-color: var(--body-theme-color);
}

.modal-movie {
  background-color: var(--modal-movie-color);
}

.switcher {
  background-color: inherit;
  border: none;
  border-radius: 18px;
  grid-template-rows: 1fr 1fr 1fr;
  padding: 2px;
  display: grid;
  position: absolute;
  top: 160px;
  right: 18px;
}

.switcher__legend {
  opacity: 0;
  pointer-events: none;
  position: absolute;
}

.switcher__radio {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  width: 18px;
  height: 18px;
  z-index: 1;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 14px;
  border-radius: 16px;
  margin: 0;
  transition: filter .2s ease-in;
}

.switcher__radio:focus-visible {
  outline: none;
}

.switcher__radio--light {
  background-image: url("light.b762eab7.svg");
}

.switcher__radio--auto {
  background-image: url("auto.37eb201e.svg");
}

.switcher__radio--dark {
  background-image: url("dark.d5332de6.svg");
}

.switcher__status {
  background-color: rgba(0, 0, 0, 0);
  background-image: url("status.3af589db.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 18px;
  border-radius: 16px;
  transition: background-position .2s ease-in;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: 0 0 0 2px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 768px) {
  .switcher {
    top: 150px;
    right: 35px;
  }
}

.switcher__radio:focus-visible ~ .switcher__status {
  box-shadow: 0 0 0 2px #000;
}

.switcher__radio--light:checked ~ .switcher__status {
  background-position: 50% 2px;
}

.switcher__radio--auto:checked ~ .switcher__status {
  background-position: center;
}

.switcher__radio--dark:checked ~ .switcher__status {
  background-position: center bottom 2px;
}

